body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#contact{ 
  text-align: center;
}
.portfolio{
  display: flex;
  flex-direction: column;
  align-items: center;
  
}
#portfolio h1 { 
  font-size: 5rem;
}
#portfolio ul { 
  margin: 10px;
  padding: 0;
  list-style: none;
  display: flex;
}
#portfolio ul li { 
  font-size: 2rem;
  margin-right: 30px;
  padding: 7px;
  
}
#portfolio .container{ 
  width: 70%;
  display: flex;
  align-items: center;
  justify-content:center;
  flex-wrap: wrap;
}
#portfolio .container .item{
  width: 220px;
  height: 250px;
  margin: 15px;
  border-radius: 20px;
}



#resume{
  text-align: center
}
#resume h2{
  text-align: center
}




#skills{
  padding:50px 15px;
  text-align:center
}
